var contactForm = new Vue({
	el: '#contactForm',

	data: {
		sending: false
	},

	methods: {
		validateBeforeSubmit: function(e) {
			this.$validator.validateAll();

            if (this.errors.any()) {
                e.preventDefault()
            }
		}
	}
})